
import Countup from 'vux/src/components/countup/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

import Spinner from 'vux/src/components/spinner/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'


export default {
components: {
Countup,
CellBox,
Group,
Card,
Spinner,
Scroller,
Flexbox,
FlexboxItem,
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
Tab,
TabItem,
Sticky,
Divider,
XButton,
Swiper,
Panel,
SwiperItem
},
data () {
return {
pageData:{
currentUser:{},
followList:[],
articleList:[],
fansList:[],
userFollow:[]
},

}
},
beforeRouteEnter:(to,from ,next)=>{
next((vm)=>{
let isLogin = vm.$store.getters.isLogin
if(!isLogin){
vm.$router.push({name:'loginLink'})
}
})
},
created() {
this.pageData.currentUser = this.$store.getters.currentUser

this.$Apis.inserttabbarSelected(4)

this.queryUserFollowEvt()

this.queryFansEvt()

this.queryFollowEvt()

this.queryArticleByUserId()
},
mounted(){

},
computed:{

},
methods:{
goUserFollow(){
this.$router.push({name:'userFollowLink'})
},
goUserFollowArticleSort(){
this.$router.push({name:'userFollowArticleSortLink'})
},
goUserFans(){
this.$router.push({name:'userFansLink'})
},

queryFansEvt(){
let params = {
user_id:this.$store.getters.currentUser.user_id
}
this.$Axios.post(this.$Urls.POST_USER_FANS,params).then(res=>res.data).then((res)=>{
if(res.code === "0000"){
if(res.data.length>0){
this.pageData.fansList = res.data
}
}else{
this.$vux.toast.text('系统错误', 'bottom')
}
}).catch(err=>{
this.$vux.toast.text('系统错误：'+err, 'bottom')
})
},

queryUserFollowEvt(){
let params = {
user_id:this.$store.getters.currentUser.user_id
}
this.$Axios.post(this.$Urls.POST_USER_FOLLOW,params).then(res=>res.data).then((res)=>{
if(res.code === "0000"){
if(res.data.length>0){
this.pageData.userFollow = res.data
}
}else{
this.$vux.toast.text('系统错误', 'bottom')
}
}).catch(err=>{
this.$vux.toast.text('系统错误：'+err, 'bottom')
})
},

queryFollowEvt(){
let params = {
user_id:this.$store.getters.currentUser.user_id
}
this.$Axios.post(this.$Urls.POST_ARTICLESORT_FOLLOW,params).then(res=>res.data).then((res)=>{
if(res.code === "0000"){
if(res.data.length>0){
this.pageData.followList = res.data
}
}else{
this.$vux.toast.text('系统错误', 'bottom')
}
}).catch(err=>{
this.$vux.toast.text('系统错误：'+err, 'bottom')
})
},

queryArticleByUserId(){
let params = {
user_id:this.$store.getters.currentUser.user_id
}
this.$Axios.post(this.$Urls.POST_ARTICLE_QUERYBYUSERID,params).then(res=>res.data).then((res)=>{
if(res.code === "0000"){
if(res.data.length>0){
this.pageData.articleList = res.data
}
}else{
this.$vux.toast.text('系统错误', 'bottom')
}
}).catch(err=>{
this.$vux.toast.text('系统错误：'+err, 'bottom')
})
}
}

}



