
import Scroller from 'vux/src/components/scroller/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default{
name: 'articleIndex',
directives: {
TransferDom
},
components:{
Scroller,
FlexboxItem,
Flexbox,
XImg,
Previewer,
ViewBox,
XHeader,
XTextarea,
XInput,
Group,
Badge,
Alert,
Toast,
Sticky,
Tab,
TabItem
},
data(){
return {
articleSort:{},
queryObj:{
sort_article_id:-1,
sort_article_name:'',
essence:'0',
currentPage:1,
pageSize:10,
count:0,
pageCount:0,
list:[]
}
}
},
created(){
this.queryObj.sort_article_id = this.$route.query.sort_article_id
this.queryObj.sort_article_name =
this.$Apis.insertLatelys(this.queryObj.sort_article_id)
this.initFunc()
},
methods:{
initFunc(){
this.queryById()
this.articleSortIndex()
},
queryById(){
let params = {
sort_article_id:this.queryObj.sort_article_id
}
this.$Axios.post(this.$Urls.POST_ARTICLESORT_QUERYBYID,params).then(res=>res.data).then((res)=>{
if(res.code === '0000'){
this.articleSort = res.data[0]
}else{
this.$vux.toast.text('系统修改查看人数错误', 'bottom')
}
}).catch(err=>console.log("系统修改查看人数错误："+err))
},
articleSortIndex(){
let params = {
sort_article_id:this.queryObj.sort_article_id,
essence:this.queryObj.essence,
currentPage:this.queryObj.currentPage,
pageSize:this.queryObj.pageSize,

}
this.$Axios.post(this.$Urls.POST_ARTICLESORT_INDEX,params).then(res=>res.data).then((res)=>{
if(res.code === '0000'){
let obj = []
res.data.list.forEach(item=>{
item.article_time = this.$Apis.dateFormat("MM-dd",new Date(item.article_time).getTime())
obj.push(item)
})
this.queryObj.list = obj
this.queryObj.currentPage = res.data.currentPage
this.queryObj.pageSize = res.data.pageSize
this.queryObj.count = res.data.count
this.queryObj.pageCount = res.data.pageCount
}else{
this.$vux.toast.text('系统修改查看人数错误', 'bottom')
}
}).catch(err=>console.log("系统修改查看人数错误："+err))
},
detailEvt(id){
this.$router.push({
name:'articleIndexLink',
query:{
article_id:id
}
})
},
essenceEvt(essence){
this.queryObj.essence = essence
this.articleSortIndex()
},

insertEvt(){
this.$router.push({
path:'/article/insert',
query:{
type_id:0,
sort_article_id:this.queryObj.sort_article_id,
sort_article_name:this.articleSort.sort_article_name
}
})
}
}
}



