
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default {
components: {
Tabbar,
TabbarItem
},
data(){
return {
tabbarSelected:1
}
},
created(){
this.tabbarSelected = window.localStorage.getItem("tabbarSelected")
}
}



