
import Scroller from 'vux/src/components/scroller/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'


export default {
components: {
XImg,
Scroller,
Badge,
Grid,
GridItem,
XHeader,
XInput,
Search,
Group,
Cell,
XButton
},
data () {
return {
pageData:{
headerIndex: 1,
selected:{},
},
results: [],
value: '',
followList:[],
latelyList:[],
isLogin:false,
boxWidth:''
}
},
created() {
this.$Apis.inserttabbarSelected(2)
this.isLogin = this.$store.getters.isLogin
if(this.isLogin){
this.queryAllByUserIdEvt()
}
this.queryLatelys()
},
methods: {
searchEvt(){
this.$router.push({
name:'searchsLink'
})
},

queryAllByUserIdEvt(){
let user = this.$store.getters.currentUser
let params = {
user_id:user.user_id
}
this.$Axios.post(this.$Urls.POST_ARTICLESORT_FOLLOW,params).then(res=>res.data).then((res)=>{
if(res.code === "0000"){
if(res.data.length>0){
this.followList = res.data
}
}else{
this.$vux.toast.text('系统错误', 'bottom')
}
}).catch(err=>{
this.$vux.toast.text('系统错误：'+err, 'bottom')
})
},
queryLatelys(){
let params = {
latelys:window.localStorage.getItem("lately")
}
this.$Axios.post(this.$Urls.POST_ARTICLESORT_LATELYS,params).then(res=>res.data).then((res)=>{
if(res.code === "0000"){
if(res.data.length>0){
this.latelyList = res.data
this.boxWidth = res.data.length*90
}else{
this.$vux.toast.text('暂无数据', 'bottom')
}
}else{
this.$vux.toast.text('系统错误', 'bottom')
}
}).catch(err=>{
this.$vux.toast.text('系统错误：'+err, 'bottom')
})
}

},

}




