
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Panel from 'vux/src/components/panel/index.vue'

import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

export default {
components: {
Panel,
Search,
Group,
Cell,
Scroller,
ViewBox,
Tab,
TabItem,
Sticky,
Divider,
XButton,
Swiper,
SwiperItem
},
data () {
return {
results: [],
pageData:{
search:{
isFocus:true,
value:'',
userList:[],
articleList:[],
articleSortList:[],
},
tabItemIndex:'1'

},
index: 0,
getBarWidth: function (index) {
return (1) * 22 + 'px'
}
}
},
created(){
this.pageData.search.value = this.$route.query.name
this.updateSearchList()
this.queryUser()
this.queryArticle()
this.queryArticleSprt()
},
mounted(){
this.setFocus()
},
methods: {
updateSearchList(){
this.$Apis.insertSearchList(this.pageData.search.value)
},
tabItemEvt(index){
this.pageData.tabItemIndex = index
},
onImgError (item, $event) {
console.log(item, $event)
},
setFocus() {
this.$refs.search.setFocus()
},
resultClick(item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult(val) {
this.pageData.search.list = []
let params = {
sort_article_name:this.pageData.search.value
}
},
queryUser(){
let params = {
name:this.pageData.search.value
}
this.$Axios.post(this.$Urls.POST_USER_LIKEUSERNAME,params).then(res=>res.data).then((res)=>{
if(res.code === '0000'){
if(res.data.length>0){
this.pageData.search.userList = res.data
}
}else{
this.$vux.toast.text('系统错误', 'bottom')
}
}).catch(err=>console.log("系统错误："+err))
},
queryArticle(){
let params = {
name:this.pageData.search.value
}
this.$Axios.post(this.$Urls.POST_ARTICLE_LIKEARTNAME,params).then(res=>res.data).then((res)=>{
if(res.code === '0000'){
if(res.data.length>0){
this.pageData.search.articleList = res.data
}
}else{
this.$vux.toast.text('系统错误', 'bottom')
}
}).catch(err=>console.log("系统错误："+err))
},
queryArticleSprt(){
let params = {
name:this.pageData.search.value
}
this.$Axios.post(this.$Urls.POST_ARTICLESORT_LIKEARTSNAME,params).then(res=>res.data).then((res)=>{
if(res.code === '0000'){
if(res.data.length>0){
this.pageData.search.articleSprtlist = res.data
}
}else{
this.$vux.toast.text('系统错误', 'bottom')
}
}).catch(err=>console.log("系统错误："+err))
},
onSubmit() {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},
onFocus() {
},
onClear(){
this.$router.push({
name:'SearchLink'
})
},
onCancel() {
this.$router.push({
name:'homeLink'
})
}
},
}



