
import Badge from 'vux/src/components/badge/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default{
name: 'articleIndex',
directives: {
TransferDom
},
components:{
XImg,
Previewer,
ViewBox,
XHeader,
XTextarea,
XInput,
Group,
Badge,
Alert,
Toast,
Sticky,
Tab,
TabItem
},
data(){
return {
queryObj:{
article_id:'',
detail:{
images:[]
}
},
options: {
getThumbBoundsFn (index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
}
}
},
created(){
this.queryObj.article_id = this.$route.query.article_id
this.updateClick()
},
methods:{
updateClick(){
let params = {
article_id:this.queryObj.article_id
}
this.$Axios.post(this.$Urls.POST_ARTICLE_UPDATECLICK,params).then(res=>res.data).then((res)=>{
if(res.code === '0000'){
this.queryEvt()
}else{
this.$vux.toast.text('系统修改查看人数错误', 'bottom')
}
}).catch(err=>console.log("系统修改查看人数错误："+err))
},
queryEvt(){
let params = {
article_id:this.queryObj.article_id
}
this.$Axios.post(this.$Urls.POST_ARTICLE_DETAIL,params).then(res=>res.data).then((res)=>{
if(res.code === '0000'){
let obj = res.data[0]
obj.article_time = this.$Apis.dateFormat("MM-dd",new Date(obj.article_time).getTime())
let images = []
obj.images.forEach(el=>{
images.push({
article_id : el.article_id,
article_image_id : el.article_image_id,
article_image_url : el.article_image_url,
src : el.article_image_url
})
})
obj.images = images
this.queryObj.detail = obj
this.$Apis.insertLatelys(this.queryObj.detail.sort_article_id)

}else{
this.$vux.toast.text('系统错误', 'bottom')
}
}).catch(err=>console.log("系统错误："+err))
},
moreEvt(){

},
logIndexChange (arg) {
console.log(arg)
},
show (index) {
this.$refs.previewer.show(index)
},
sortArticleEvt(sort_article_id){
this.$router.push({
name: 'articleSortIndexLink',
query: {
sort_article_id: sort_article_id
}
})
}

}
}



