
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'


export default {
components: {
Search,
Group,
Cell,
Scroller,
ViewBox
},
data () {
return {
results: [],
value: '',
pageData:{
search:{
isFocus:true,
value:'',
list:[]
},
searchList:[]
}
}
},
created(){
this.getSearchList()
},
mounted(){
this.setFocus()
},
methods: {
getSearchList(){
this.pageData.searchList = []
if(window.localStorage.getItem("searchList")!=null){
let obj = window.localStorage.getItem("searchList").split(',')
obj.forEach(item=>{
this.pageData.searchList.push({
sort_article_name:item
})
})

}
},
setFocus() {
this.$refs.search.setFocus()
},
resultClick(item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult(val) {
this.pageData.search.list = []
let params = {
name:this.pageData.search.value
}
console.log('on-change', val)
if(this.pageData.search.value !=''){



this.$Axios.post(this.$Urls.POST_ARTICLESORT_LIKEARTSNAME,params).then(res=>res.data).then((res)=>{
if(res.code === '0000'){
if(res.data.length>0){
this.pageData.search.list = res.data
}else{
this.pageData.search.list.push({
sort_article_name:this.pageData.search.value
})
}
this.pageData.search.isFocus = false;
}else{
this.$vux.toast.text('系统错误', 'bottom')
}
}).catch(err=>console.log("系统错误："+err))

}
},
searchNameEvt(item){
this.$router.push({
name:'SearchIndexLink',
query:{
name:item.sort_article_name
}
})
},
onSubmit() {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},
onFocus() {
},
onClear(){
this.pageData.search.isFocus = true;
},
onCancel() {
this.$router.push({
name:'homeLink'
})
}
},

}




