
import XButton from 'vux/src/components/x-button/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Divider from 'vux/src/components/divider/index.vue'

export default{
name: 'userFollow',
directives: {
TransferDom
},
components:{
Divider,
XButton,
Scroller,
XImg,
ViewBox,
XHeader,
},
data(){
return {
queryObj:{
list:[]
}
}
},
beforeRouteEnter:(to,from ,next)=>{
next((vm)=>{
let isLogin = vm.$store.getters.isLogin
if(!isLogin){
vm.$router.push({name:'loginLink'})
}
})
},
created() {
this.queryAllByUserIdEvt()
},
methods:{

queryAllByUserIdEvt(){
let user = this.$store.getters.currentUser
let params = {
user_id:user.user_id
}
this.$Axios.post(this.$Urls.POST_ARTICLESORT_FOLLOW,params).then(res=>res.data).then((res)=>{
if(res.code === "0000"){
if(res.data.length>0){
this.queryObj.list = res.data
}
}else{
this.$vux.toast.text('系统错误', 'bottom')
}
}).catch(err=>{
this.$vux.toast.text('系统错误：'+err, 'bottom')
})
},
}
}



