

import Vue from 'vue'
import App from './App'

import store from './store/index'

import VueRouter from 'vue-router'
import {routes} from './router/index'
Vue.use(VueRouter)


import UrlMapping from './assets/js/UrlMapping'
Vue.prototype.$Urls = new UrlMapping()
import Axios from './assets/js/Axios'
Vue.prototype.$Axios = Axios
import Apis from './assets/js/Apis'
Vue.prototype.$Apis = new Apis()


window.vueEvent = new Vue();
window.that = Vue


import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import ConfigPlugin from 'vux/src/plugins/config/index.js'

Vue.use(ConfirmPlugin)
Vue.use(AlertPlugin)
Vue.use(LoadingPlugin)
Vue.use(ToastPlugin)
Vue.use(ConfigPlugin, {
$layout: 'VIEW_BOX'
})


let router = new VueRouter({
mode:'history',
routes
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
el: '#app',
components: { App },
router,
store,
template: '<App/>'
})



