
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Countup from 'vux/src/components/countup/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import Spinner from 'vux/src/components/spinner/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'


export default {
directives: {
TransferDom
},
components: {
LoadMore,
XImg,
Countup,
Spinner,
Scroller,
Flexbox,
FlexboxItem,
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
Tab,
TabItem,
Sticky,
Divider,
XButton,
Swiper,
Panel,
SwiperItem
},
data () {
return {
queryObj:{
currentPage:1,
pageSize:10,
count:0,
pageCount:0,
list:[]
},
pullupEnabled: true,
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},
pageData:{
headerNav: ['关注','首页','视频'],
headerIndex: 1,
selected:{},
swiperHeight:'0'
},

}
},
created() {
this.$Apis.inserttabbarSelected(1)
this.pageData.swiperHeight = (document.documentElement.clientHeight-46-50)+"px"
this.queryEvt()
},
methods:{
searchEvt(){
this.$router.push({
name:'searchsLink'
})
},
loadMore () {
setTimeout(() => {
this.queryObj.currentPage++
if(this.queryObj.currentPage<this.queryObj.pageCount){
this.queryEvt()
}
setTimeout(() => {
this.$refs.scroller.donePullup()
}, 10)
}, 2000)
},
refresh () {
setTimeout(() => {
this.queryObj.currentPage = 1
this.queryObj.list = []
this.queryEvt()
this.$nextTick(() => {
setTimeout(() => {
this.$refs.scroller.donePulldown()
this.pullupEnabled && this.$refs.scroller.enablePullup()
}, 10)
})
}, 2000)
},
queryEvt(){
let params = {
currentPage:this.queryObj.currentPage,
pageSize:this.queryObj.pageSize
}
this.$Axios.post(this.$Urls.POST_ARTICLE_INDEX,params).then(res=>res.data).then((res)=>{
if(res.code === '0000'){
res.data.list.forEach(el=>{
el.article_time = this.$Apis.dateFormat("MM-dd",new Date(el.article_time).getTime())
this.queryObj.list.push(el)
})
this.queryObj.currentPage = res.data.currentPage
this.queryObj.pageSize = res.data.pageSize
this.queryObj.count = res.data.count
this.queryObj.pageCount = res.data.pageCount
}else{
this.$vux.toast.text('系统错误', 'bottom')
}
}).catch(err=>console.log("系统错误："+err))
},
detailEvt(id){
this.$router.push({
name:'articleIndexLink',
query:{
article_id:id
}
})
},
sortArticleEvt(sort_article_id){
this.$router.push({
name: 'articleSortIndexLink',
query: {
sort_article_id: sort_article_id
}
})
}

}


}



